<template>
  <div class="main-with-login">
    <div class="main-with-login-body">
      <header>
        <div class="logo-area header-area">
          <img class="main-logo-text" src="@/assets/logo_text.png" @click="$router.push('/')" alt="text_logo">
        </div>
        <div class="button-area">
          <img src="@/assets/btn_hamburger.png" alt="nav_btn" @click="openNavBar">
        </div>
      </header>

      <main>
          <div class="action-level">
            <div class="action-level-img">
              <p> </p>
            </div>
          </div>
          <div class="mcol-button-area">
            <common-button
                v-for="(btn, idx) in buttonData"
                @click="moveToPage(btn.src)"
                width="250"
                height="50"
                marginRight="40"
                background="white"
                :buttonName="btn.name"
                fontSize="25"
                borderRadius="50"
                border="none"
                :key="idx"/>
          </div>
          
          <div class="main-col">
            <p class="main-col-title">
              
            </p>
            <div class="main-col-area">
              <common-collection
                @click="openModal(col, true)"
                v-for="(col, idx) in sampleData.dataSet" 
                :info="col" 
                :id="idx"
                :key="idx" />
            </div>
          </div>
          <div class="main-col main-col-analysis">
            <p class="main-col-title">
              
            </p>
            <div class="analysis-area">
              <div class="analysis-button-area" 
                  v-for="(btn, idx) in analysis"
                  :key="idx">
                <p>{{ btn.name }}</p>
              </div>
            </div>
          </div>
        <navigator-bar ref="navBar" />
      </main>
      
      <footer>
        <main-footer/>
        <common-modal ref="modal" />
      </footer>

    </div>
  </div>
</template>

<script>
import CommonButton from '@/components/CommonButton.vue';
import CommonCollection from '@/components/CommonCollection.vue';
import MainFooter from '@/components/MainFooter.vue'
import NavigatorBar from '../components/NavigatorBar.vue';
import SampleData from '@/assets/sampleData.json';
import CommonModal from '@/components/CommonModal.vue'


export default {
  components: { CommonButton, CommonCollection, MainFooter, NavigatorBar, CommonModal },
  name: "MyCollection",
  data() {
    return {
      loginBool: false,
      sampleData: {dataSet:[]},
      buttonData: [
        {
          name: ' ', 
          src: '/mcol/note',
        },
        {
          name: '  ', 
          src: '/mcol/store',
        },
        {
          name: 'Collection ', 
          src: '/mcol/mc',
        },
        {
          name: 'Community ', 
          src: '/com/mcom',
        }
      ],
      analysis: [
        {
          name: ' ', 
          src: ''
        },
        {
          name: '   ', 
          src: ''
        },
        {
          name: '  ', 
          src: ''
        },
      ]
    }
  },
  methods: {
    openNavBar() {
      this.$refs.navBar.openNavBar()
    },
    openModal(colData, moveToPageBool) {
      this.$refs.modal.openModal(colData, moveToPageBool);
    },
    moveToPage(src) {
      console.log(src);
      this.$router.push(src);
    },
    createDummies(store, start) {
      for(var i = 0; i < 5; i++) {
        var random = Math.floor(Math.random()*10);
        store.dataSet[i] = {};
        store.dataSet[i].id = start;
        store.dataSet[i].author = 'Author....' + i;
        store.dataSet[i].nickName = 'NickName....' + i;
        store.dataSet[i].title = 'Title....' + i;
        store.dataSet[i].content = 'Content...' + i;
        store.dataSet[i].folder = 'FolerNo...' + i;
        store.dataSet[i].src = [SampleData.imgUrl[random], SampleData.imgUrl[random+1], SampleData.imgUrl[random+2]];
        store.dataSet[i].hashTag = ['HashTag...'+i, 'HashTag...'+(i+1), 'HashTag...'+(i+2)];
        store.dataSet[i].cuCo = 'Collection';
        store.dataSet[i].regDate = '2022-03-01';
        store.dataSet[i].modDate = '2022-03-02';

        start++;
      }
    }
  },
  created() {
    this.createDummies(this.sampleData, 1);
  }
}
</script>

<style scoped>
.main-with-login-body {
  max-width: 1200px;
  min-width: 1200px;
  width: 100vw;
  height: 100vh;
  /* background: lightcoral; */
  margin: 0 auto;
}

/* header  */
header {
  width: 100%;
  height: 135px;
  /* background: lightblue; */
  position: relative;
  display: flex;
  justify-content: flex-end;
}
.header-area {
  width: 70%;
}
.main-logo-text {
  width: 113px;
  height: 39px;
  margin-top: 60px;
  /* position: absolute; */
  left: 0;
  top: 0;
  cursor: pointer;
}
.button-area {
  width: 30%;
  /* background: red; */
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 63px;
}
.button-area img {
  margin-right: 20px;
  cursor: pointer;
}

/* main  */

.action-level {
  width: 100%;
  height: 450px;
  /* background: lightgreen; */
  display: flex;
  justify-content: center;
}
.action-level-img {
  width: 250px;
  height: 250px;
  background: white;
  border-radius: 125px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
}
.mcol-button-area {
  display: flex;
  height: 200px;
}

/*   */
.main-col {
  width: 100%;
  max-width: 1200px;
  height: 420px;
  /* background: green; */
  /* padding: 0 100px; */
}
.main-col-title {
  margin-bottom: 100px;
  font-size: 30px;
  font-weight: bold;
}
.main-col-area {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.main-col-analysis {
  height: 300px;
}
.analysis-area {
  padding: 0 40px;
  display: flex;
  justify-content: space-around;
}
.analysis-button-area {
  width: 300px;
  height: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #DC8552;
  border-radius: 12px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
}
</style>